body {
  cursor: url("./assets/cursor/Sakura_Arrow.cur"), auto;
}

ul.list-sakura {
  list-style-type: "‚ùÄ ";
}

.birthday.navbar, .birthday.footer {
  background-color: #ef476f !important;
}

.anniversary.navbar, .anniversary.footer {
  background-color: #e63946 !important;
}

#overlay {
  position: fixed;
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.5);
  z-index: 2;
  cursor: pointer;
}

.container-fluid .row+.row, .card-body .row+.row {
  margin-top: 1rem;
}

.footer {
  background-color: #20232a;
  color: #fff;
  font-size: 1.15rem;
  font-size: 1rem;
  height: 100px;
  margin: auto;
  padding: 1.5rem 0.5rem;
  width: 100%;
}

.footer .buttons {
  text-align: center;
  margin-bottom: 0.25rem;
}

.footer .buttons .button {
  margin-bottom: 0;
  margin-right: 0 !important;
  color: #fff;
  background-color: transparent;
  border-color: transparent;
  padding-left: 0.5em;
  padding-right: 0.5em;
  transition: color 0.2s ease-out;
}

.footer .buttons .button:hover {
  color: #61dafb;
}

.footer .buttons .button:first-child {
  margin-left: auto !important;
}

.footer .buttons .button:last-child {
  margin-right: auto !important;
}

.footer .buttons .button .icon {
  align-items: baseline;
}

.footer .content {
  text-align: center;
  word-spacing: 0.05rem;
}